.header {
  padding-top: 20px;
}

.header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;  
  max-width: 1110px;
  margin: auto;
}

.header__txt {
  text-align: center;
  color:#fff;
  font-family: $font-1-b;
  font-size: 16px;
  line-height: 20px;
}

.header__phone-link {
  font-family: $font-1-b;
  font-size: 27px;
  color:#fff;
  text-decoration: none;
}

.header__phone-code {
  color: $color-1;
}

.header__phone-btn {
  font-family: $font-1-b;  
  font-size: 16px;
  border-bottom: 1px dashed #fff; 
  color: #fff;
  cursor: pointer;
}

