.main {
  
}

.main__wrap {
  margin: auto;
  margin-top: 80px;
  max-width: 570px; 
  padding-bottom: 20px;
}

.main__ttl {
  font-family: $font-1-b;
  font-size: 32px;  
  color: #fff;
  text-align: center;
}

.main__ttl-price {
  color: $color-1;
}

.main__txt {
  font-family: $font-1-b;
  font-size: 24px;
  color: #fff;
  text-align: center;
  margin-top: 45px;
}

.main__select {
  margin-top: 45px;
}

.main__select-ttl {
  font-family: $font-1-b;
  font-size: 16px;
  color: #fff;
  text-align: center;
  margin-top: 45px;
}

.main__select {
  @include flex-row;
  margin: auto;
  justify-content: space-between;
  width: 250px;
  margin-top: 10px;
}

.main__select-item {
  width: 122px; 
  font-size: 10px;
}

.mark-select {
  margin-right: 5px;
}

.main__open-kit {  
  @include btn;
  width: 330px; 
}



