.kit {
  display: none;
  margin-top: 40px;   
}

@keyframes show__kit {
  0% {
    background: rgba(255,255,255, 0.9);    
  }
  50% {
    background: rgba(255,255,255, 0.8);
  }
  100% {
    opacity: 1;
  }
}

.kit__open {
  display: block;
  opacity: 1;  
  animation: show__kit 0.4s ease;
}

.kit__wrap {
  max-width: 1110px;
  margin: auto;
}

.kit__svghere {
  margin:0 auto;
  position: relative;
  background-image:url(%=static=%/content/fon.png); 
  width: 440px; 
  height: 680px; 
  background-size: 451px 667px;
  background-position: -10px 0px;
  background-repeat: no-repeat;
}

.kit__overlay {
  position: absolute;
  width:100%;
  height:100%;
  background-image: url(%=static=%/content/shadow_border.png);
  background-size: 451px 667px;
  background-position: -10px 0px;
  background-repeat: no-repeat;
  z-index: 10;
  top:0;
}

.kit__svg {
  transition: all ease 0.1s;
  width:100%;
  fill: rgba(0,0,0,0);
  fill-rule:evenodd;
  position: relative;
  z-index: 12;  
}

.kit__wrap-svg {   
  fill: url(#bg);  
  stroke: rgba(0,0,0, 0.1);
  stroke-width: 0;
}

.kit__main-hover:hover,
.kit__side-hover:hover,
.kit__back-hover:hover{
  fill:rgba(255,0,0,0.7);
  cursor: pointer;
}

.kit__parts-items {
  @include flex-row;
  @include nl;  
  margin: auto;
  width: 650px;
}

.kit__parts-item {
  @include flex;
  align-items: center;
  justify-content: center;
  font-family: $font-1-b;
  text-align: center;
  background: #fff;
  border-radius: 7px;
  padding: 5px;
  width: 128px;
  font-weight: 16px;
  cursor: pointer;  
  margin-right: 11px;
  margin-left: 11px;
  &:hover {
    box-shadow: 0px 0px 5px 1px rgba(255,255,255, 0.5);
  }
}

.kit__parts-item--active {
  background: $color-2;
  color: #fff;
  transition: 0.2s ease;
}

.kit__option {
  @include flex-row;
  justify-content: space-between;
  margin-top: 44px;
}

.kit__stitch-wrap {
  border-radius: 8px;
  width: 323px;
  padding: 17px;
  background: #fff;  
  display: none;
}

.kit__choose-open {
  display: block;
  animation: show__kit 0.2s ease;
}

.kit__stitch-ttl {
  display: block;
  font-family: $font-1-b;
  font-size: 16px;
  border-bottom: 1px solid #000;
  padding-bottom: 5px;
}

.kit__stitch-items {
  @include nl;
  @include flex-row;
  flex-wrap: wrap;
  margin-top: 15px;
  margin-bottom: 35px;
}


.kit__stitch-type {
  display: block;
  font-family: $font-1-l;
  font-size: 16px;
  border: 1px solid #000;
  border-radius: 10px;
  width: 136px;
  text-align: center;
  margin-bottom: 10px;
  cursor: pointer;
  &:not(:nth-of-type(2n)) {
    margin-right: 10px;
  }
  &:hover {
    box-shadow: 0px 0px 5px 1px rgba(0,0,0, 0.5);
  }
}

.kit__stitch-type--active {
  background: $color-2;
  color: #fff;
  border-color: $color-2;
}

.kit__stitch-type-input {
  display: none;
}


.kit__show-price {
  @include btn; 
  width: 329px;
  margin-top: 30px;
}

.kit__stitch-color-items {
  @include nl;
  width: 256px;
  @include flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.kit__stitch-color-item {
  width: 74px;
}

.kit__slider-wrap {
  @include flex-row;
  align-items: center;
  justify-content: space-between;
}

.kit__slider-prev {
  display: block;
  @include bg($kit__prev);  
  cursor: pointer;
}

.kit__slider-next {
  display: block;
  @include bg($kit__next);  
  cursor: pointer;
}

.kit__slider-wrap {
  display: none;
}

.kit__all-hover {  
  stroke-width: 1px;
  stroke-opacity: 0.5;
}

.kit__main-hover {
  stroke-width: 0px;
}

.kit__side-hover {
  stroke-width: 2px;
}

.kit__back-hover {
  stroke-width: 6px;
}

.kit__stitch-img {
  cursor: pointer;
}

.kit__stitch-input {
  display: none;
}

.kit__type--active {
  display: flex;
}

.kit__stitch-name {
  display: block;
  font-size: 12px;
  font-family: $font-1-b;
  line-height: 12px;  
  margin-top: 10px;
  text-align: center;
  word-wrap: break-word;
}