// Mixins.

@mixin nl {
    margin: 0;
    padding: 0;
    text-indent: 0;
    list-style: none;
    list-style-position: outside;
    text-decoration: none;
}

@mixin cf {
    &:before,
    &:after {
        display: table;
        content: "";
    }

    &:after {
        clear: both;
    }

    .lt-ie8 & {
        zoom: 1;
    }
}

@mixin blocks-justify {
    text-align: justify;
    text-justify: newspaper;
}

// You can add your own mixins here:

// =============================================================================
// Font Face
// =============================================================================

// $name - название для font-family
// $path - путь к шрифту без формата
// $weight - font-weight
// $style - font-style
// $exts - форматы шрифта, которые используются
@mixin font-face($name, $path, $weight: null, $style: null, $exts: eot woff2 woff ttf svg) {
    $src: null;

    $extmods: (
        eot: "?",
        svg: "#" + str-replace($name, " ", "_")
    );

    $formats: (
        otf: "opentype",
        ttf: "truetype"
    );

    @each $ext in $exts {
        $extmod: if(map-has-key($extmods, $ext), $ext + map-get($extmods, $ext), $ext);
        $format: if(map-has-key($formats, $ext), map-get($formats, $ext), $ext);
        $src: append($src, url(quote($path + "." + $extmod)) format(quote($format)), comma);
    }

    @font-face {
        font-family: quote($name);
        font-style: $style;
        font-weight: $weight;
        src: $src;
    }
}


@mixin clearfix {
    &:after {
        display: table;
        content: "";
    }

    &:after {
        clear: both;
    }
}

@mixin center() {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

// Flex

@mixin flex-row() {  
  display: flex;
  flex-direction: row;
}

@mixin flex() {  
  display: flex;
  flex-direction: column;
}


// Fonts //////////////

// Text

@mixin text($size: 18px, $color: #fff, $line: auto, $weight: 400) {
  font-family: $font-1, Arial, sans-serif;
  font-weight: $weight;
  font-size: $size;
  color: $color;
  line-height: $line;
}

@mixin text-bold($size: 18px, $color: #fff, $line: auto, $weight: 400) {
  font-family: $font-1-bold, Arial, sans-serif;
  font-weight: $weight;
  font-size: $size;
  color: $color;
  line-height: $line;
}

@mixin text-l($size: 18px, $color: #fff, $line: auto, $weight: 400) {
  font-family: $font-1-light, Arial, sans-serif;
  font-weight: $weight;
  font-size: $size;
  color: $color;
  line-height: $line;
}

@mixin dot() {
  height: 40px;
  width: 40px;  
  @include flex;
  justify-content: center;
  align-items: center;
  margin: 5px; 
  &:after {
    content: '';
    display: block;
    background: #fff;
    border-radius: 50%;
    height: 8px;
    width: 8px;
  }
}


@mixin input() {
  border: 1px solid #f8f8f8;
  border-width: 1px;
  border-radius: 7px;
  background: #f8f8f8;
  font-family: $font-2-l;
  color: $color-1;
  font-size: 16px;
  padding: 11px;  
  padding-left: 40px;  
  width: 190px;
  margin-top: 15px;
  outline: none;
  &:hover {
    background: #fff;
    border: 1px solid $color-4;
  }
  &:focus {
    border-color: $color-4;
  }
}

@mixin title() {
  @include center();  
  font-family: $font-1;
  font-size: $h2-desktop;
  color: $color-1;
  border-bottom: 10px double $color-9;
}

@mixin btn() {
  cursor: pointer;
  display: block;
  font-size: 16px;
  margin: auto;
  margin-top: 45px;
  color: #fff;
  font-family: $font-1-b;
  border-radius: 5px;
  padding: 15px;
  background: linear-gradient(to bottom, #dd0004, #510000);
  width: 330px;
  text-align: center;
  text-decoration: none;
  transition: 0.2s ease;
  &:hover {
    box-shadow: 0px 0px 10px 4px rgba(0,0,0, 0.5);
    transition: 0.2s ease;
  }
  &:active {
    opacity: 0.7; 
  }
}

@mixin line() {
  height: 2px;
  width: 70px;
  background: $color-2;    
}

































