.footer__wrap {
  @include flex-row;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  padding-bottom: 21px;
  max-width: 1110px;  
}

.footer__firm {
  font-family: $font-1-b;
  color: #fff;
  font-size: 16px;
}

.footer__social {
  @include flex-row;
  @include nl;
  align-items: center;
}

.footer__soc-item {
  margin-right: 10px;
  margin-left: 10px;
}

.footer__privacy-policy {
  font-family: $font-1-m;
  color: #fff;
  border-bottom: 1px dashed #fff;
  font-size: 16px;
  line-height: 16px;  
}